'use client'
import React, { useEffect, useState } from "react";
import styles from "../NavBar/NavBar.module.css";
import { useRouter } from "next/navigation";

const NavBar = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const router = useRouter();

  useEffect(() => {
    // Check if accessToken exists in localStorage
    const token = localStorage.getItem("accessToken");
    setIsLoggedIn(!!token);
  }, []);

  const handleAddPost = () => {
    router.push("/add-post"); // Navigate to your AddPost page
  };

  const handleLogin = () => {
    router.push("/login"); // Navigate to login page
  };

  return (
    <div className={styles.navbar}>
      <div className={styles.navbarContent}>
        <p className={styles.navbarItem}>Home</p>
        <p className={styles.navbarItem}>Creative Writing</p>
        <p className={styles.navbarItem}>Poems</p>
        <p className={styles.navbarItem}>Short Stories</p>
        <p className={styles.navbarItem}>Literary Analysis</p>
        <p className={styles.navbarItem}>Entertainment</p>

        {/* Show Add Post only if logged in */}
        {isLoggedIn && (
          <button
            className={styles.addPostBtn}
            onClick={handleAddPost}
          >
            Add Post
          </button>
        )}
      </div>

      <input
        type="search"
        placeholder="Search posts..."
        className={styles.navbarSearch}
      />

      {/* Login button if logged out */}
      {!isLoggedIn && (
        <p
          className={styles.navbarItem}
          onClick={handleLogin}
          style={{ cursor: "pointer" }}
        >
          Log in
        </p>
      )}
    </div>
  );
};

export default NavBar;
